.header-container{
    height: 30px;
    width: 80%;
    background-color: teal;
    position: fixed;
    top: 0px;
    right: 0px;
}
ul{
    list-style: none;
    margin: 0;
    padding: 0;
    top:30px;
    position: relative;
}
li{
    display: block;
    padding: 8px;
    border-bottom: 1px solid orangered;
    color: orangered;
    font-weight: 700;
    width: 60%;
    margin: auto;
}

li:hover{
    cursor: pointer;
    color: aqua;
    border-bottom: 1px solid aquamarine;
}


