.nav-container{
    height: 100%;
    position: fixed;
    top:0px;
    left:0px;
    width:20%;
    background-color: #051821;
}
.avatar-container{    
    margin-top:50px;
    margin-left: 23%;
    height:150px;
    border: 1.5px solid orangered;
    border-radius: 75px;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}
.avatar-container img{
    height:150px;
    border-radius:75px;
}
.twitter-handle{
   margin-top: 20px; 
   font-size: smaller;     
}
.twitter-handle img{
    height:22px;    
    vertical-align: middle;
    padding-right: 2px;
}
.twitter-handle a{
    line-height: 10px;
   color: orange;
}
.twitter-handle a:hover{
    color: skyblue;
}


